
import { Button, Navbar, Container, Nav } from 'react-bootstrap'
import 이미지 from './main.png'
import data from './data.js'
import { useState , useEffect} from 'react'
import { Routes, Route, Link, useNavigate, Outlet } from 'react-router-dom'
import Detail from './routes/Detail';
import Cart from './routes/Cart';
import axios from 'axios';
import './App.css';

function App() {

  useEffect(( ) => {
    localStorage.setItem('watched' , JSON.stringify([]))
  },)


  let [shoes, SetShoes] = useState(data);
  let navigate = useNavigate();
  let [butCount, setButCount] = useState(0);


  return (
    <div className="App">

      <Navbar bg="dark" variant="dark">
        <Container>
          <Navbar.Brand href="#home">React Shopping Mall</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={() => { navigate('/') }}>HOME</Nav.Link>
            <Nav.Link onClick={() => { navigate('/detail/0') }}>DETAIL</Nav.Link>
          </Nav>
        </Container>
      </Navbar>


      <Routes>
        <Route path="/" element={
          <>
            <div className="main-bg" style={{ backgroundImage: 'url(' + 이미지 + ')' }}></div>
            <div className='container'>
              <div className='row'>
                {shoes.map(function (a, i) {
                  return (
                    <Product 신발={shoes[i]} 인덱스={i} />
                  )
                })}

              </div>
            </div>
            <button onClick={() => {
              switch (butCount) {


                case 0:
                  axios.get('https://codingapple1.github.io/shop/data2.json')
                    .then((결과) => {
                      let copy = [...shoes, ...결과.data]
                      SetShoes(copy);
                    }).catch(() => {
                      console.log('실패')
                    })
                  setButCount(butCount + 1);
                  break;
                case 1:
                  axios.get('https://codingapple1.github.io/shop/data3.json')
                    .then((결과) => {
                      let copy = [...shoes, ...결과.data]
                      SetShoes(copy);
                    }).catch(() => {
                      console.log('실패')
                    })
                  setButCount(butCount + 1);
                  break;
                case 2:
                  alert('더 없다')
              }



            }}>버튼</button>
          </>
        } />


        <Route path="/detail/:id" element={<Detail 신발={shoes} />} />
        <Route path="/cart" element={<Cart />} />


      </Routes>
    </div>
  );
}





function Product(props) {
  let pic = `https://codingapple1.github.io/shop/shoes${props.인덱스 + 1}.jpg`;
  return (
    <div className='col-md-4' key={props.인덱스} >
      <img src={pic} width='80%' />
      <h4> {props.신발.title} </h4>
      <p> {props.신발.price}원</p>
    </div>
  )
}


export default App;
