import React, { useEffect, useState } from "react";
import { Fade, Nav } from "react-bootstrap";
import { useParams } from "react-router-dom";
import styled from "styled-components";
import { addItem } from "../store";
import { useDispatch } from "react-redux";





function Detail(props) {




    let dispatch = useDispatch();
    let [count, setCount] = useState(0);
    useEffect(() => {

        setTimeout(() => {
            setAlert(false)
        }, 2000)
        return () => {

        }
    }, [count])



    let { id } = useParams();




    let 찾은상품 = props.신발.find((x) => x.id == id)
    var 꺼낸거 = JSON.parse(localStorage.getItem('watched'));
    꺼낸거.push(찾은상품.id)
    localStorage.setItem('watched', JSON.stringify(꺼낸거))

    
    
    
    let [alert, setAlert] = useState(true);
    let [탭, 탭변경] = useState(0);
    return (

        <div className='container'>

            {
                alert == true ? <div className="alert alert-warning">
                    2초이내 구매시 할인
                </div> : ""
            }
            {count}
            <button onClick={() => { setCount(count + 1) }}>버튼</button>
            <div className='row'>
                <div className='col-md-6'>
                    <img src={'https://codingapple1.github.io/shop/shoes' + (찾은상품.id + 1) + '.jpg'} width="100%" />
                </div>
                <div className='col-md-6'>
                    <h4 className='pt-5'>{찾은상품.title}</h4>
                    <p>{찾은상품.content}</p>
                    <p>{찾은상품.price} 원</p>
                    <button className='btn btn-danger' onClick={() => {
                        dispatch(addItem({ id: 1, name: 'eee', count: 4 }))
                    }}>주문하기</button>
                </div>
            </div>

            <Nav variant='tabs' defaultActiveKey='link0'>
                <Nav.Item>
                    <Nav.Link onClick={() => { 탭변경(0) }} eventKey='link0'>버튼0</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link onClick={() => { 탭변경(1) }} eventKey='link1'>버튼1</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link onClick={() => { 탭변경(2) }} eventKey='link2'>버튼2</Nav.Link>
                </Nav.Item>
            </Nav>
            <탭컨텐트 탭={탭} />
        </div>
    )
}

function 탭컨텐트(props) {
    let [fade, setFade] = useState('');
    useEffect(() => {
        setTimeout(() => { setFade('end') }, 100)
        return () => {
            setFade('')
        }
    }, [props.탭])

    return <div className={'start' + fade}> {[<div>내용0</div>, <div>내용1</div>, <div>내용2</div>][props.탭]} </div>
}
export default Detail;